<template>
    <div id = "menu">
      <div id="list-please-container">
      <p id="list-please">
Concurrent Users {{connectNum}}
</p>
      </div>
      <div id="button-container" >
<div id="button-first">
  <p id="connectCap" > 
    Screen connection is complete! <br>This website contains only a portion of the entire list.
  </p>
         
</div>
<div id="button-second">
                <router-link to="/controllist"><img id="list-link" v-on:click="emitEvent('displaylist')" src = "../../assets/svg/right.svg"></router-link>

</div>
      </div>
    </div>
</template>

<script>
const jsons = require("../../assets/titles.json")

import * as TWEEN from '@tweenjs/tween.js';
export default {
  sockets: {
   connectCountDeliv:function(data){
      console.log(data)
      this.connectNum=data-1;
    
  },colorChangeAct:function(data){
this.gradientNum=data;
 this.colorChangeAnimation(this.gradientNum);

  }
  },
  data(){
    return{
      gradientNum:0
    }
  },
methods:{
    listpleaseAnimation:function(){
                     var second= TweenMax.to("#list-please", 1, {ease: Linear.easeNone,css:{left:"-400%"}, repeat: -1 });
                      var currentDuration = second.duration();
                      second.duration(15); 
                  
    },  emitEvent: function(data) {
      this.$socket.emit("controlAction", data);
      console.log("clicked");
    }, colorChangeAnimation:function(e){
      for (var i = 0; i < jsons.length; i++){
  // look for the entry with a matching `code` value
  var nowColor;
if (jsons[i].num == e){
    nowColor=jsons[i].color;
  }
}
console.log(nowColor);
TweenMax.to("#button-container", 1, {backgroundImage:`linear-gradient(to top,${nowColor[0]},${nowColor[1]},${nowColor[2]})`, repeat:0});
    }
  },mounted(){
  this.listpleaseAnimation();

  },data(){
    return {
      connectNum:0
    }
  }
}
</script>

<style scoped>
#menu {
  display: flex;
  flex-direction: column;
  height:92%;

}
a {
  text-decoration: none;
}
a:visited {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}
#list-please-container{
background-color:black;
height:auto;

}
#list-please{
color:white;

  font-size: 1.5em;
  font-weight: 500;
  text-align: center;
 position: relative;
  white-space: nowrap;
  left:100%;
  overflow:visible;
  margin:5px;
  text-transform: uppercase;
  
}
#list-link{
  color:black;

  
}
#button-second{

  margin: 0 auto;

position:relative;
  max-height:350px;
  width:60%;
  margin-top:20%;
  

}
#button-second img{
width:100%;
filter: drop-shadow(0px 0px 8px #4444dd);
}

#connectCap{
  font-size:0.5em;
  text-align :center;
}
#button-first{
  margin: 0 auto;
 width:85%;

margin-top:5%;
 
}
#button-first p{
  margin: 0;
  font-size:1.7em;
   text-align :left;
   font-family:matter;
  font-weight: 700;
  padding-right:5%;

}
#button-container{
    mix-blend-mode: difference;
   display: flex;
  flex-direction: column;

     height:100%;
    background-image: linear-gradient(to top, #000, #fff, #000, #fff, #000);


}
</style>



